.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.venue-wrapper {
		position: relative;
		.venue-content {
			position: relative;
			.card {
				background-color: transparent;
				border: none;
				text-align: center;
				font-family: $font-secondary;
				.card-body {
					position: relative;
					padding: 0 1rem;
					.event-description {
						position: relative;
						margin-bottom: 1rem;
						p {
							margin-bottom: 0;
						}
					}
					.event {
						position: relative;
						width: 100%;
						&-name {
							position: relative;
							span {
								display: block;
								letter-spacing: 0.5rem;
								font-size: 18px;
								margin-bottom: 0.5rem;
								@include max-sm {
									font-size: 16px;
									letter-spacing: 0.25rem;
								}
							}
						}
						&-date {
							position: relative;
							span {
								display: block;
								letter-spacing: 0.15rem;
							}
							.day {
								font-size: 42px;
								text-transform: uppercase;
								letter-spacing: 0.75rem;
								margin-bottom: 0.5rem;
								@include max-lg {
									font-size: 34px;
								}
								@include max-sm {
									font-size: 28px;
								}
							}
						}
						p {
							margin-bottom: 0;
							font-weight: 600;
						}
					}
					.event-place {
						position: relative;
						margin-top: 3rem;
						&-title {
							position: relative;
							span {
								display: block;
								font-size: 18px;
								letter-spacing: 0.5rem;
								margin-bottom: 0.5rem;
								@include max-sm {
									font-size: 16px;
									letter-spacing: 0.25rem;
								}
							}
						}
						&-name {
							position: relative;
							.title {
								display: block;
								font-size: 42px;
								letter-spacing: 0.75rem;
								margin-bottom: 0.5rem;
								@include max-lg {
									font-size: 38px;
								}
								@include max-sm {
									font-size: 20px;
									letter-spacing: 0.5rem;
								}
							}
						}
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 3rem;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
