.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		.image-element {
			position: relative;
			width: 100%;
			.thank-background {
				width: 100%;
				height: auto;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 100%;
			transform: translate(-50%, -50%);
			padding: 3rem 2rem;
			color: var(--color-light);
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
